import { Component, OnInit } from '@angular/core';
import { ConsultaService } from '../../services/consulta.service';
import { GetinteligenceService } from '../../services/getinteligence.service';
import { Consulta, Sintom, Patient } from '../../models/consulta';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
import { ZONAS, SUBZONAS } from '../../models/inteligence';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { DoctorHomeService } from '../../services/doctor-home.service';


@Component({
  selector: 'app-nuevaconsulta',
  templateUrl: './nuevaconsulta.component.html',
  styleUrls: ['./nuevaconsulta.component.css']
})
export class NuevaconsultaComponent implements OnInit {

  isCollapsed = false;
  consulta: any = [];
  //sintoms: any = [];
  closeResult: string;
  //sintom: any;
  isintomas: any;
  currConsulta: any = {};
  response: any;
  //izonas = ZONAS;
  //bevelzonas = SUBZONAS;
  //subzonaview: any = null;
  //sintomMeasures: any = [];
  symForm: FormGroup;
  SignalForm: FormGroup;
  doctors:any=[];



  constructor(
    private consultaService: ConsultaService,
  ) { }

  ngOnInit() {

    this.symForm = new FormGroup({
      symptom: new FormControl(null, Validators.required),
      zone: new FormControl(null),
      intencity: new FormControl(null, Validators.required)
    });
  
    this.SignalForm = new FormGroup({
      name: new FormControl(null, Validators.required),
      measure: new FormControl(null),
      units: new FormControl(null, Validators.required)
    });
    this.consulta = {
      patient:{
        name:'Unnamed',
        age:'12',
        gender:'M'
      },
      symptoms:[],
      signals:[],
      info:{},
    };

    this.doctors = [];
    this.setConsulta();
  }



  onSubmit() {
    return;
  }



  
  setConsulta(): void {
    this.consultaService.setConsulta().subscribe(
      response => { 
        localStorage.removeItem('appoinment');
        localStorage.setItem('appoinment', JSON.stringify(response));    
      }
    );
  }

getCurrAppoinment(){
  const appoinment = localStorage.getItem('appoinment');
  const appoinmentjson = JSON.parse(appoinment);
  return appoinmentjson;
}

  
  getDoctors(): void {
    const appoinment = this.getCurrAppoinment();
    this.consultaService.getDoctors(appoinment.pk).subscribe(
      response => { this.doctors = response;  }
    );
  }


  setSymptom(): void {
    const newsin = {
      symptom:this.symForm.controls['symptom'].value,
      zone:this.symForm.controls['zone'].value,
      intencity:this.symForm.controls['intencity'].value

    };
    const appoint = this.getCurrAppoinment();
    this.consultaService.setSymtom(appoint.pk, newsin).subscribe(
      response => { 
        this.consulta.symptoms.push(newsin);
        this.symForm.reset();
        this.getDoctors();
          }
    );
  }

  rmSym(thSym){
    const inx = this.consulta.symptoms.indexOf(thSym);
    this.consulta.symptoms.splice(inx,1);
    this.getDoctors();
  }


  setSignal(){
    const newsignal = {
      name:this.SignalForm.controls['name'].value,
      measure:this.SignalForm.controls['measure'].value,
      units:this.SignalForm.controls['units'].value

    };
    this.consulta.signals.push(newsignal);
    this.SignalForm.reset();
  }


  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }


}
